import React from 'react';
import img1 from './images/chota bheem.jpg';


function About(){
    return(
        <div>
            <h1 align="center">chota bheem</h1>
            <img src={img1} width='300' height='300'></img>
            <h3>Chhota Bheem  and the Curse of Damyaan is a 2012 Indian animated action-adventure fantasy drama film written and directed by Rajiv Chilaka, based on the character Chhota Bheem and his friends. It is the eleventh movie in the Chhota Bheem film series and the first movie to be released in theatres. The film was made under the Green Gold Animation banner in association with INOX Cinemas and distributed by PVR Pictures worldwide.[.</h3>
           
        </div>
    )
}
export default About;